body{
	font-color: white;
	background-color: black;
}

.cut-out{
		width: 100%;
		background-image: url(../../assets/img/cut-out.png);
		background-size: cover;
		padding-bottom: 10%;

	}
.pinstripe-wrap{
	padding-top: 10%;
	padding-bottom: 10%;
	width: 82%;
	margin: 0 auto;
	p, h1, h2,h3, {
	font-color: white;

}


	    
	.pinstripe-head{
	    padding-bottom: 4%;
	    border-bottom: 1px solid RGB(139, 139, 139);
	    margin-bottom: 10%;
		h1{
			color: white;
		    font-size: 2rem;
		    letter-spacing: 3px;
		    text-transform: uppercase;
		    margin-bottom: 2%;
		}

		.bread-crumbs{
		color: RGB(139, 139, 139);
		font-size: .8rem;
	    display: flex;
	    justify-content: space-between;
	    width: 17%;
	    text-transform: uppercase;

		}
	}

	.pinstripe-content{
		    display: flex;
		    flex-direction: row;
		    color: white;
		    flex-wrap: wrap;
		.product-image{
		    width: 36%;
		    display: flex;
		    justify-content: space-evenly;
		    margin-right: 3%;
			img{
				height: 500px;
				margin-right: 10%;
			}
			
		}

		.product-description{
			width: 60%;
		    display: flex;
		    flex-direction: column;
		    flex-wrap: wrap;
		    

		    .product-description-head{
		    	display: flex;

		    	.text-wrap{
		    		h1{
	    			    font-size: 1.5rem;
					    margin-bottom: 10px;
					    color: RGB(50, 206, 208);
					    font-style: italic;
		    		}
		    	}
			    
			    img{
			    	height: 74px;
			    	max-width: 115px;
			    	margin-right: 2%;
			    }
		    }

		    p{
		    	width: 90%;
	    	    margin: 0 auto;
	    	    margin-bottom: 5%;
		    	color: RGB(139, 139, 139);
		    	line-height: 30px;
		    	font-size: .9rem;
		    }

		    .customize{
		    	display: flex;
		    	margin-bottom: 5%;
		    	padding-left: 7%;
		    	p{
	    			color: RGB(50, 206, 208);
	    			text-transform: uppercase;
	    			letter-spacing: 2px;
	    			font-size: .7rem;
		    	}

		    	.customize-color{
	    		    display: flex;
				    width: 30%;
				    justify-content: space-around;
				    align-items: center;
				    margin-right: 20%;
					.color-option-1{
						width: 34px;
						height: 30px;
						border-radius: 100%;
						background-color: rgb(135, 85, 0);
						margin-right: 10px;					 
					}
					.color-option-2{
						width: 34px;
						height: 30px;
						border-radius: 100%;
						background-color: rgb(12, 213, 196);
						margin-right: 10px;
					}
					.color-option-3{
						width: 34px;
						height: 30px;
						border-radius: 100%;
						background-color: rgb(216, 123, 0);
						margin-right: 10px;
					}

		    	}

		    	.customize-size{
		    		display: flex;
		    		align-items: center;

		    		p{
		    			margin-right: 20%;
		    		}

		    		select{
	    			    background: transparent;
					    color: RGB(139, 139, 139);
		    		}
		    	}
		    }
		    .price{	
		    		width: 31%;
		    		justify-content: space-around;
		    		display: flex;
		    		align-items: center;
		    		margin-bottom: 5%;
		    		padding-left: 7%;


		    		p{
	    			color: RGB(50, 206, 208);
				    text-transform: uppercase;
				    letter-spacing: 2px;
				    font-size: .7rem;
		    		}

		    		h1{
		    			font-size: 1.6rem;
		    		}
		    	}

		    	.specifics-wrap{
		    		display: flex;
		    		margin-top: 10%;
		    		padding-top: 10%;
				    border-top: 1px solid RGB(139, 139, 139);
		    		text-transform: uppercase;
		    		justify-content: space-between;

		    		.left-half{
	    			    display: flex;
					    flex-direction: column;

					    button{
					    	background-color: transparent;
					    	border: 1px solid RGB(139, 139, 139) ;
					    	color: RGB(139, 139, 139);
					    }
					    

					    h1{
				    	    font-size: 1rem;
			    	        margin-bottom: 20px;
			    	        letter-spacing: 1.5px;
					    }

					   	.functions, .hardware, .ply {
					   		margin-bottom: 10%;
					   		h2{
					    	margin-bottom: 5%;
					    	color: RGB(139, 139, 139);
					    	font-size: .8rem;
					    	}

					    	h3{
					    		color: RGB(50, 206, 208);
					    		font-size: .8rem;
					    	}
					   	}
					    
		    		}

		    		.right-half{
		    			margin-bottom: 5%;

		    			h1{
		    				font-size: 1rem;
		    				margin-bottom: 20px;
			    	        letter-spacing: 1.5px;
		    				margin-bottom: 20px;
		    			}

		    			iframe{
		    				max-width: 370px;
		    				height: 83%;
		    			}
		    		}
		    	}

		    	.related-items-wrap{
		    		width: 100%;
	    		    margin-top: 10%;
	    		    padding-top: 5%;
				    border-top: 1px solid RGB(139, 139, 139);

		    		.related-items-head{
	    			    width: 100%;
					    display: flex;
					    justify-content: space-between;
					    align-items: center;
					    margin-bottom: 5%;

					    h1{
					    	text-transform: uppercase;
					    	letter-spacing: 1.4px;

					    }

					    button{
					    	width: 107px;
					    	margin-top: unset;
					    	background-color: transparent;
					    	border: 1px solid;
					    	color: RGB(139, 139, 139);

					    }
		    		}

		    		.related-items-slider{
		    			width: 100%;

		    			ul{
		    				display: flex;
		    			}
		    		}
		    	}
		}

		button{
		    width: 170px;
		    height: 35px;
		    border-radius: 50px;
		    background-color: RGB(247, 76,43);
		    color: white;
		    text-transform: uppercase;
		    margin-top: 5%;
		    font-size: 1rem;
		    border: none;
		}
	}
}